<template>
  <b-container :fluid="true" class="">
    <b-overlay :show="!productLoaded" spinner-large
        spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
      <b-carousel
        id="carousel-1"
        :interval="4000"
        controls
        indicators
        img-width="200"
        img-height="200"
        background="gray"
        style="text-shadow: 1px 1px 2px #333;"
        @sliding-start="onSlideStart"
        @sliding-end="onSlideEnd"
        class="mb-4"
      >
        <b-carousel-slide v-for="(image1,index) in brand.images" :key="index">
          <template v-slot:img>
            <img width="200"
              height="300"
              background-color="red"
              :src=image1.link
              class="d-block w-100"
            >
          </template>
        </b-carousel-slide>
      </b-carousel>
      <b-row>
        <h1 class="mx-auto text-center">{{this.$route.query.name}}</h1>
      </b-row>
      <hr>
      <product-sorter :products="products"></product-sorter>
      <b-row>
        <b-col v-for="product in products" :key="product.id" class="" cols="6" sm="4" md="3" lg="2">
          <product-box :product="product"></product-box>
        </b-col>
      </b-row>
      <hr>
      <b-container fluid class="d-flex justify-content-center" v-if="productLoaded">
        <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
          <b-button :disabled="busy" v-if="!productLoadingDone" style="background-color: #007bff" @click="loadProducts(brand.name)">
            Load More Products
          </b-button>
        </b-overlay>
      </b-container>
    </b-overlay>
  </b-container>
</template>

<script>
import ProductBox from '../../components/Boxes/ProductBox'
import ProductSorter from '../../components/ProductSorter.vue'
import loader from '../../mixins/loader'

export default {
  name: 'CertainBrand',
  title () {
    return this.$route.query.name
  },
  data () {
    return {
      brand: {},
      products: [],
      productLoadingDone: true,
      sliding: null,
      busy: false,
      productLoaded: false
    }
  },
  // computed: {
  //   imgSrc () {
  //     if (this.brand.images) {
  //       return this.brand.images
  //     } else if (this.brand.image) {
  //       return this.brand.image
  //     }
  //     return ''
  //   }
  // },
  components: {
    ProductBox,
    ProductSorter
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadBrand (brandName) {
      this.loader('/brand', { name: brandName }, `brand ${brandName}`)
        .then(response => {
          this.brand = response.data
        })
    },
    loadProducts (brandName) {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/brand',
        { name: brandName, offset, limit },
        `products of ${brandName}`)
        .then(response => {
          this.busy = false
          this.productLoaded = true
          this.products = this.products.concat(response.data.products)
          this.productLoadingDone = response.data.products.length < limit
        })
    },
    clearProducts () {
      this.products = []
      this.productLoadingDone = false
    },
    loadAll (brandName) {
      this.loadBrand(brandName)
      this.clearProducts()
      this.loadProducts(brandName)
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadAll(to.query.name)
    }
  },
  created () {
    this.loadAll(this.$route.query.name)
  }
}
</script>

<style scoped>
.main {
  background-color: rgb(221, 221, 221);
}
</style>
