<template>
  <div>
    <b-carousel
      id="carousel-1"
      :interval="4000"
      controls
      indicators
      img-width="200"
      img-height="200"
      background="gray"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="mb-4 mt-4"
    >
      <b-carousel-slide
        v-for="(image1,index) in images" :key="index">
        <template v-slot:img>
          <img width="200"
               height="300"
               background-color="red"
               :src=image1.link
               class="d-block w-100"
          >
        </template>
      </b-carousel-slide>
    </b-carousel>
    <b-container>
      <div class="mx-auto text-center mb-4"><h1>Popular Shops</h1></div>
      <b-row cols-lg="3" cols-md="2">
        <b-col v-for="(brand,idx) in brands" cols="12" md="6" :key="idx">
          <b-card header-tag="header">
            <template v-slot:header >
              <router-link :to="{name: 'CertainBrand',query: { name: brand.name}}">
                <h4 class="text-center">{{brand.name}}</h4>
              </router-link>
              <div class="mx-auto text-center">
                <b-form-rating inline no-border
                  style="background-color:inherit;color:#0f76e4;"
                  readonly
                  size="sm"
                  v-model="brand.rating"
                  precision="1">
                </b-form-rating>
              </div>
            </template>
            <b-card-text>
              <b-row class="no-gutters" cols="3"  style="">
                  <b-col v-for="(product,id) in brand.products" :key="id">
                    <div class="border border-white" style="background-color:#f0f0f0;"
                          @click="$router.push({ name: 'CertainProduct', params: { id: product.id } })" >
                      <!-- <b-img :src="product.image.link" center height="100%" width="100%"></b-img>
                      <div class="mx-auto text-center text-truncate" style="height:100%;width:100%">
                       {{product.name}}
                      </div> -->
                      <b-card>
                          <b-card-img ref="img" :src="product.image.link" :alt="product.name" height="100px" top style="border-radius: 0;">
                          </b-card-img>
                          <b-card-text>
                            <div class="text-truncate text-center">
                              {{product.name}}
                            </div>
                        </b-card-text>
                      </b-card>
                    </div>
                  </b-col>
              </b-row>
            </b-card-text>
          </b-card>
        </b-col>
      </b-row>
      <hr>
      <div style="border: 0px solid black; background-color: white;">
        <div class="mx-auto text-center"><h1>Products</h1></div>
        <product-scroll-section :products="relatedProducts"></product-scroll-section>
      </div>
      <!-- <pre>{{relatedProducts}}</pre>
      <pre>{{images}}</pre> -->
      <!-- <pre>{{brands}}</pre> -->
      <div class="mx-auto text-center"><h1>Shops</h1></div>
      <b-row>
        <b-col v-for="(brand, idx) in brands" :key="idx" lg="2" md="3" sm="4" cols="6" class="my-2 px-2">
          <brand-box :brand="brand"></brand-box>
        </b-col>
      </b-row>
      <b-container fluid class="d-flex justify-content-center">
        <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
          <b-button style="background-color: #007bff" :disabled="busy" v-if="!productLoadingDone" @click="loadMore">
            Load More
          </b-button>
        </b-overlay>
      </b-container>
      <hr>
    </b-container>
  </div>
</template>

<script>
import loader from '../../mixins/loader'
import BrandBox from '../../components/Boxes/BrandBox'
import ProductScrollSection from '../../components/ProductScrollSection'

export default {
  name: 'PopularBrands',
  title: 'Popular Brands',
  data () {
    return {
      brands: [],
      productLoadingDone: true,
      busy: false
    }
  },
  computed: {
    relatedProducts () {
      const limit = 10
      return this.brands.filter(brand => brand.products && brand.products.length).map(brand => brand.products[brand.products.length - 1]).slice(0, limit)
    },
    images () {
      const limit = 6
      return this.brands.filter(brand => brand.images && brand.images.length).map(brand => brand.images[0]).slice(0, limit)
    }
  },
  components: {
    BrandBox,
    ProductScrollSection
  },
  mixins: [
    loader
  ],
  methods: {
    getBrandLink (item) {
      return {
        name: 'CertainBrand',
        query: {
          name: item
        }
      }
    },
    loadMore () {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.brands.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/top-brands', { offset, limit }, 'top-brands')
        .then(response => {
          this.brands = this.brands.concat(response.data.brands)
          this.busy = false
          this.productLoadingDone = response.data.brands.length < limit
        })
    },
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    }
  },
  created () {
    this.loadMore()
  }
}
</script>

<style scoped>
.card-body {
  padding: 2%;
}
</style>
