<template>
  <b-container :fluid="true" class="main">
    <b-row>
      <h1 class="mx-auto my-2 text-center">All Shops</h1>
    </b-row>
    <b-overlay :show="!productLoaded" spinner-large
        spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
      <b-row>
        <b-col v-for="brand in brands" :key="brand.name" lg="2" md="3" sm="4" cols="6" class="mt-3 mb-2">
          <brand-box :brand="brand"></brand-box>
        </b-col>
      </b-row>
      <b-container fluid class="d-flex justify-content-center">
        <b-overlay :show="busy" opacity="0.4" spinner-small
                          spinner-variant="primary" class="d-inline block">
          <b-button v-if="!brandLoadingDone" class="my-3" style="background-color: #007bff" :disabled="busy" @click="loadBrands">
            Load More
          </b-button>
        </b-overlay>
      </b-container>
    </b-overlay>
    <hr>
  </b-container>
</template>

<script>
import BrandBox from '../../components/Boxes/BrandBox'
import loader from '../../mixins/loader'

export default {
  name: 'AllBrands',
  title: 'Brands',
  data () {
    return {
      brands: [],
      brandLoadingDone: true,
      busy: false,
      productLoaded: false
    }
  },
  components: {
    BrandBox
  },
  mixins: [
    loader
  ],
  methods: {
    loadBrands () {
      if (!this.brandLoadingDone) this.busy = true
      const offset = this.brands.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/brands/all', { offset, limit }, 'brands')
        .then(response => {
          this.productLoaded = true
          this.busy = false
          this.brands = this.brands.concat(response.data.brands)
          this.brandLoadingDone = response.data.brands.length < limit
        })
    }
  },
  created () {
    this.loadBrands()
  }
}
</script>

